<template>
    <div>
        <!-- 数据表单 -->
        <Modal
                :title="dataFormTitle"
                v-model="dataFormShow"
                :width="modelWidth"
                class-name="vertical-center-modal"
        >
            <Form :label-width="formLabelWidth" :model="dataForm"  :ref="refName"  :rules="ruleValidate">
                <template v-for="(item,index) in fieldsData">
                    <template v-if="item.data_form == true">
                        <template v-if="item.data_type == 'hidden'">
                            <input type="hidden" v-model="dataForm[item.slot]" >
                        </template>
                        <template v-else>
                            <Row>
                                <FormItem :label="item.title"  :prop="item.slot" >
                                    <Select @on-change="dataFormShow = true" style="width:200px"  v-model="dataForm[item.slot]" filterable  placeholder="请选择" v-if=" item.data_type == 'select' ">
                                        <Option
                                                v-for="(option_item,option_index) in item.options"
                                                :key="option_index"
                                                :value="option_index">{{ option_item }}</Option>
                                    </Select>

                                    <DatePicker
                                            v-else-if=" item.data_type == 'date' "
                                            v-model="dataForm[item.slot]"
                                            type="date"
                                            format="yyyy-MM-dd"
                                            placeholder=""
                                            @on-change="(datetime) =>{ changeDatetime(datetime,item.slot)}"
                                            @on-open-change="dataFormShow = true"
                                    >
                                    </DatePicker>

                                    <DatePicker
                                            v-else-if=" item.data_type == 'datetime' "
                                            v-model="dataForm[item.slot]"
                                            type="datetime"
                                            format="yyyy-MM-dd HH:mm:ss"
                                            placeholder=""
                                            @on-change="(datetime) =>{ changeDatetime(datetime,item.slot)}"
                                            @on-open-change="dataFormShow = true"
                                    >
                                    </DatePicker>

                                    <vc-upload @on-upload-success="uploadSuccess" :upload-file-server="uploadFileServer" :extension="item.extension"  :upload-file-max-size="uploadFileMaxSize"  :ref="item.slot" :prop="item.slot"  v-else-if=" item.data_type == 'image' || item.data_type == 'file' "></vc-upload>

                                    <vue-neditor-wrap  v-else-if=" item.data_type == 'editor' "
                                            v-model="dataForm[item.slot]"
                                            :config="editorConfig"
                                            :destroy="false"
                                            @ready="editorReady">
                                    </vue-neditor-wrap>


                                    <Input v-model="dataForm[item.slot]" :placeholder="'请输入' + item.title" v-else=" item.data_type == 'string' "></Input>

                                </FormItem>

                            </Row>
                        </template>

                    </template>


                </template>
            </Form>
            <div slot="footer" class="dialog-footer">
                <Button  type="default" @click="resetDataForm">重置</Button>
                <Button type="primary" @click="saveDataForm()">保存</Button>
            </div>

        </Modal>

    </div>
</template>

<script>

    import VueNeditorWrap from 'vue-neditor-wrap'
    import VcUpload from './upload.vue'


    export default {
        name:'vc-form',
        props:['dataFormTitle','modelWidth','formLabelWidth','dataForm','ruleValidate','fieldsData','refName','uploadFileServer','uploadFileMaxSize','editorConfig'],
        components:{VcUpload,VueNeditorWrap},
        data(){
            return {
                editorCfg: {
                    // 如果需要上传功能,找后端小伙伴要服务器接口地址
                    serverUrl: '',
                    // 你的UEditor资源存放的路径,相对于打包后的index.html
                    UEDITOR_HOME_URL: '',
                    // 编辑器不自动被内容撑高
                    autoHeightEnabled: false,
                    // 初始容器高度
                    initialFrameHeight: 240,
                    // 初始容器宽度
                    initialFrameWidth: '100%',
                    // 关闭自动保存
                    enableAutoSave: false
                },
                dataFormShow: false
            }
        },
        updated:function () {
            //处理iview的DatePicker时间带T带Z格式问题
            let that = this
            that.fieldsData.forEach(function (val,key) {
                if(val.data_type == 'date' ||  val.data_type == 'datetime'){
                    that.dataForm[val.slot] = that.getFormatDate(that.dataForm[val.slot])
                }
            })

        },
        mounted: function () {
            this.editorCfg = this.editorConfig
            this.editorCfg.serverUrl = this.uploadFileServer
            console.log(this.editorCfg)

        },
        methods:{
            editorReady(text){
                console.log(text)
            },


            uploadSuccess: function (uploadList,prop) {
                let fileList = []
                uploadList.forEach(function (v,k) {
                    fileList.push(v.url)
                })
                this.dataForm[prop] = fileList.join(',')
            },
            //处理iview的DatePicker时间带T带Z格式问题
            getFormatDate: function(dateStr){
                let dateJson = new Date(dateStr).toJSON()
                let dateRes = new Date(+ new Date(dateJson) + 8 * 3600 * 1000).toISOString().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'')
                return dateRes
            },
            //重置数据表单
            resetDataForm: function () {
                this.$refs[this.refName].resetFields()
            },
            //保存数据表单
            saveDataForm: function () {
                this.$emit('on-save-data-form',this.dataForm)
            },
            //处理日历控件数据
            changeDatetime: function (datetime, prop) {
                this.dataForm[prop] = datetime
                this.dataFormShow = true
            },
        }
    }
</script>